import { Injectable } from '@angular/core';
import * as Web3 from 'web3/src';
import * as  logisticContract from '../../../build/contracts/ParcelDelivery.json';
//import * as  logisticContract from '../../../build/contracts/ParcelDeliverySecondVersion.json';
import * as TruffleContract from 'truffle-contract';
import { Observable } from 'rxjs';


declare var window: any;


@Injectable({
  providedIn: 'root'
})
export class Web3LogisticService {
  web3: any;
  contract: any;
  contractDetails: any = "";
  contractAddress = "";
  gas = 6721975;

  constructor() {
    console.log("web3 service logistic");
    if (typeof window.web3 !== 'undefined') {
      // Use Mist/MetaMask's provider
      this.web3 = new Web3(window.web3.currentProvider);
    } else {
      this.web3 = new Web3(new Web3.providers.HttpProvider('http://localhost:8545'));
    }

    var accounts = this.web3.eth.getAccounts();
    new Promise((resolve, reject) => { resolve(accounts) }).then((data) => {
      for (let i in data) {
        if (data[i] != null) {
          this.web3.eth.defaultAccount = data[i];
          break;
        }
      }
    });

    this.initializeContract();


  }


  async initializeContract() {
  await  new Promise((resolve, reject) => { resolve(logisticContract) }).then((data) => {
      let abiFound = false;
      let addressFound = false;
      for (let i in data) {
        if (data[i] != null) {
          data[i] = (JSON.stringify(data[i]));
          if (!addressFound) {
            if ((data[i]).toString().indexOf("address") >= 0) {
              var indexStart = data[i].toString().indexOf("address");
              this.contractAddress = data[i].substr(indexStart + 10, indexStart + 10);
              //  console.log(this.contractAddress);
              //  console.log(this.contractAddress.length);
                if(this.contractAddress.includes("\"")){
                  this.contractAddress = data[i].substr(indexStart + 10, indexStart + 9);
                }
              addressFound = true;
            }
          }
          if (!abiFound) {
            if ((data[i]).toString().indexOf("abi") >= 0) {
              //     console.log(data[i]);
              //  console.log((data[i]).toString().indexOf("abi"));
              this.contractDetails = JSON.parse(data[i]);
              abiFound = true;
              this.contract = new this.web3.eth.Contract(this.contractDetails, this.contractAddress, { gas: this.gas, gasPrice: '20000000000', from: this.web3.eth.defaultAccount });
            }
          }
        }
      }
    });
       
  }


  convert(stringValue): any {
    if (stringValue != null && stringValue != true && stringValue != false) {
      let result = this.web3.utils.hexToAscii(stringValue).replace(/\0[\s\S]*$/g, '');
      //let result = this.web3.utils.hexToUtf8(stringValue);
      if (result != undefined) {
        return result;
      }
      else {
        return "";
      }
    }
    else {
      return "";
    }
  }


  showResult(result) {
    console.log("result is shown ");

    for (let value in result) {
      // if (typeof (result[value] == 'string')) {
      //   if ((result[value]).toString().indexOf('0x') != -1) {
      //     console.log("changed result: " + value + ", "+this.web3.utils.hexToUtf8(result[value]));
      //   }
      //   else {
      console.log(value + "," + this.convert(result[value]));
      //    }
      // }
    }
  }



  private createRandomID(): string {
    var text = "";
    var possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    for (var i = 0; i < 5; i++)
      text += possible.charAt(Math.floor(Math.random() * possible.length));
    return text;
  }



  submitParcel() {
    this.contract = new this.web3.eth.Contract(this.contractDetails, this.contractAddress, { gas: this.gas, gasPrice: '20000000000', from: this.web3.eth.defaultAccount });
    let parcleIDBefore = this.createRandomID();
    console.log("id before ", parcleIDBefore);
    let parcelIDAfter = this.web3.utils.asciiToHex(parcleIDBefore.substr(0, 31));
    console.log("id after ", parcelIDAfter);

    try {
      this.contract.methods.submitNewParcel2(parcelIDAfter).send({ from: this.web3.eth.defaultAccount, gas: this.gas }, (error, result) => {
        console.log("parcel submitted  " + result + ", error " + error);
      });
    }
    catch (e) {
      console.log(e);
    }

    return parcleIDBefore;

  }


  updateParcelStatus(parcelID, status) {
    console.log("default address :" + this.web3.eth.defaultAccount);

    try {

      parcelID = this.web3.utils.asciiToHex(parcelID.trim().substr(0, 31));
      status = this.web3.utils.asciiToHex(status);
      console.log(parcelID, ",  ", status);
      this.contract.methods.updateStatus2(parcelID, status).send({ from: this.web3.eth.defaultAccount, gas: this.gas }, (error, result) => {
        console.log("parcel status updated  " + result + ", error " + error);
      });
    }
    catch (e) {
      console.log(e);
    }
  }


  async getParcelStatus(parcelID) {
    this.contract = new this.web3.eth.Contract(this.contractDetails, this.contractAddress, { gas: this.gas, gasPrice: '20000000000', from: this.web3.eth.defaultAccount });
         
    let transaction;
    let parcelStatus: Observable<any[]>[] = [];
    try {
      // console.log("id before: " + parcelID);
      parcelID = this.web3.utils.asciiToHex(parcelID.trim());
      console.log("id after", parcelID);
      transaction = await this.contract.methods.getParcelStatus2(parcelID).call({ from: this.web3.eth.defaultAccount, fromBlock: "latest" }, (error, result) => {
        // this.showResult(result);            
        new Promise((resolve, reject) => { resolve(result) }).then((data) => {
          for (let i in data) {
            if (data[i] != null) {
              //console.log(data[i]);
              parcelStatus.push(this.convert(data[i]));
            }
          }
        });
        return result;
      });
    }
    catch (e) {
      console.log(e);
    }

    this.getAllParcels();

    return await parcelStatus;

  }



  async getAllParcels() {
    console.log("all parcels call");
    let transaction;
    let parcelStatus: Observable<any[]>[] = [];
    try {
      transaction = await this.contract.all_Parcels.call({ from: this.web3.eth.defaultAccount }, (error, result) => {
        // this.showResult(result);
        console.log(result, ", error, ", error);
        new Promise((resolve, reject) => { resolve(result) }).then((data) => {

          for (let i in data) {
            if (data[i] != null) {
              console.log(data[i]);
            }
          }
        });
        return result;
      });
    }
    catch (e) {
      // console.log(e);
    }

  }





}
