import { Component, OnInit } from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs';
import { LoginService } from '../login.service';

import { LoginGuardService } from '../login-guard.service';
import { Router } from '@angular/router';


@Component({
  selector: 'app-total-views',
  templateUrl: './total-views.component.html',
  styleUrls: ['./total-views.component.css']
})

export class TotalViewsComponent {
  menuActive: boolean = false;
  menuSubject=new BehaviorSubject<boolean>(false);
  dynamicSearch = new BehaviorSubject<boolean>(false);

  user_name: any;
  constructor() {
    
  }

  showMenuBar() {
    this.menuSubject.next(this.menuActive = !this.menuActive);
    this.dynamicSearch.next(false);
  }

  hideMenuBar() {
    this.menuSubject.next(this.menuActive = false);
  }
}



@Component({
  selector: 'app-menu-view',
  templateUrl: './menu-view.html',
  styleUrls: ['./total-views.component.css']
})

export class MenuComponent { 
  menuActive: boolean;
  user_name: any;
  totalCliksFromClient = new Subject<Number>();  
  constructor(private totalViews: TotalViewsComponent, private loginService: LoginService, private router: Router, private loginGuard: LoginGuardService) {
    this.totalViews.menuSubject.subscribe(state => {
      this.menuActive = state;
    });
 
    
    this.loginService.user_name.subscribe(e => {
      this.user_name = e;
    });

    window.addEventListener("beforeunload", e => {
      var confirmationMessage = "\o/";
      this.totalViews.menuSubject.next(false);
      alert("exit");
      (e || window.event).returnValue = confirmationMessage;
      return confirmationMessage;
    });
  }

  logOut(){
    console.log("logged out");    
    this.loginGuard.result=false; 
    this.loginService.user_name.next("");
    this.router.navigate([{ outlets: { 'data-app': ['login'] } }]);
  }
 
}
