import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { LoginViewComponent } from './login-view/login-view.component';
import { Routes, RouterModule, RouterStateSnapshot } from '@angular/router';
import { TotalViewsComponent, MenuComponent } from './total-views/total-views.component';
import { LoginGuardService } from './login-guard.service';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MatButtonModule, MatCheckboxModule, MatSidenavModule, MatToolbarModule, MatListModule, MatIconModule, MatGridListModule, MatCardModule, MatMenuModule, MatStepperModule, MatFormFieldModule, MatInputModule } from '@angular/material';

import { HttpClientModule } from '@angular/common/http';
import { BrowserModule } from '@angular/platform-browser';
import { Web3Pipe } from './web3-pipe';
import { LoginService } from './login.service';
import { IpfsService } from './ipfs.service';

import { LayoutModule } from '@angular/cdk/layout';
import { Web3LogisticService } from './web3-logistic.service';
import { ClientViewComponent } from './client-view/client-view.component';



export const appRoutesLogisticApp: Routes = [
  { path: '',  component: ClientViewComponent, outlet: "data-app"},
  { path: 'login', component: LoginViewComponent, outlet: "data-app" },
  { path: 'totalViews', canActivate: [LoginGuardService], component: TotalViewsComponent, outlet: "data-app" },
  { path: 'client-view', component: ClientViewComponent, outlet: "data-app" }


]

export const appRouting = RouterModule.forRoot(appRoutesLogisticApp, { useHash: true });
export const routingComponents = [LoginViewComponent, TotalViewsComponent
];



@NgModule({
  imports: [
    CommonModule,
    appRouting,
    HttpClientModule,
    ReactiveFormsModule,
    BrowserModule,
    FormsModule,
    BrowserAnimationsModule,
    MatButtonModule,
    MatCheckboxModule,
    MatSidenavModule,
    MatToolbarModule,
    MatListModule,
    MatIconModule,
    MatGridListModule,
    MatCardModule,
    MatMenuModule,
    LayoutModule,
    MatStepperModule,
    MatFormFieldModule,
    MatInputModule
  ],

  declarations: [
    LoginViewComponent,   
    MenuComponent,
    routingComponents,
    TotalViewsComponent, Web3Pipe, ClientViewComponent],

  exports: [
    LoginViewComponent,
    TotalViewsComponent],
  providers: [LoginGuardService, LoginService, LoginViewComponent, MenuComponent, TotalViewsComponent,
    IpfsService, Web3LogisticService]

})
export class LogisticAppModule { }
