import { Injectable } from '@angular/core';
import {BehaviorSubject} from 'rxjs'
import { ActivatedRoute, Router } from '@angular/router';
import { LoginGuardService } from './login-guard.service';



@Injectable()
export class LoginService {
  public user_name = new BehaviorSubject<string>("");
  currentUser: any;
  currentPasswort: any;
  loginComplete = false;
  passwort = new BehaviorSubject<string>("");


  constructor(private route: ActivatedRoute, private router: Router, private loginGuard: LoginGuardService) {
    this.user_name.subscribe(e => {
      this.currentUser = e;
    });
    this.passwort.subscribe(p => {
      this.currentPasswort = p;
    })
  }

  changeLoginName(name: string, password: string) {
    this.loginGuard.result = true;
    this.loginComplete = true;
    this.user_name.next(name);
    this.passwort.next(password);
    console.log("this url ", this.loginGuard.url);
    
    this.router.navigateByUrl(this.loginGuard.url);
  }


}
