import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'app';

  ngOnInit() {

  }


  dropDown() {

  }





  hideElements() {
    document.getElementById("contentMenu").style.visibility = "hidden";
    var startTime = new Date().getTime();
    var interval = setInterval(() => {
      if (new Date().getTime() - startTime > 200) {
        clearInterval(interval);
        document.getElementById("contentMenu").style.visibility = "visible";
        return;
      }
      //do whatever here..
    }, 200);

  }

  ngOnDestroy() {

  }

  demos: string[] = [
    'Data Management',
    'Banks',
    'Energie',
    'Health Care',
    'Logistic',
    'Insurance'].map(n => n + ' Demo');

  final = this.demos.length;
  demo = 1; // current demo



  constructor(private activatedRoute: ActivatedRoute, private router: Router) { }


  selectDemo(demo: number) {
    this.demo = demo + 1;

    console.log("index demo: " + this.demo);
    if (this.demo == 1) {
      this.router.navigate([{ outlets: { 'data-app': ['data-view'] } }]);
    }
  
  }
}


