import { Component, OnInit, Input } from '@angular/core';
import { BreakpointObserver, Breakpoints, BreakpointState } from '@angular/cdk/layout';
import { map } from 'rxjs/operators';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Subject, Observable } from 'rxjs';

import { LoginService } from '../login.service';
import { IpfsService } from '../ipfs.service';
import { Router } from '@angular/router';
import { Web3LogisticService } from '../web3-logistic.service';


@Component({
  selector: 'app-test-view',
  templateUrl: './client-view.component.html',
  styleUrls: ['./client-view.component.css']
})

export class ClientViewComponent implements OnInit {

  @Input() parcelID: any = "";
  parcelStatus: any = "";
  parcelStatusFromBlockchain: any[] = [];

  // controls for form fields
  isLinear = true;
  requiredFormGroup: FormGroup;

  // checkbox properties
  checked = false;
  indeterminate = false;
  labelPosition = 'after';
  disabled = false;




  isHandset$: Observable<boolean> = this.breakpointObserver.observe(Breakpoints.Handset)
    .pipe(
      map(result => result.matches)
    );

  constructor(private breakpointObserver: BreakpointObserver, private _formBuilder: FormBuilder, private web3: Web3LogisticService, private loginService: LoginService, private router: Router) {

  }


  ngOnInit() {
    console.log(this.loginService.currentUser);
    // Set input in fields to be required by user
    this.requiredFormGroup = this._formBuilder.group({
      status1: ['', Validators.required]
    });
  }



  public submitParcel() {
    console.log(this.parcelID);
    this.parcelID = this.web3.submitParcel();
    this.parcelStatusFromBlockchain=[];
  }


  async  showParcelStatus() {
    console.log("parcelID ", this.parcelID);

    if (this.parcelID == null || this.parcelID === undefined) {
      this.parcelStatusFromBlockchain = [];
    }
    else {
      if (this.parcelID != null) {
        let result = await this.web3.getParcelStatus(this.parcelID);
        this.parcelStatusFromBlockchain = [];
        let message = "";
        for (let i in result) {
          if (result[i].toString().includes('#')) {
            message += result[i].toString().replace('#','');
            this.parcelStatusFromBlockchain.push(message);
            message = "";
          }
          else {
            message += result[i];
          }
        }
        this.parcelStatusFromBlockchain.push(message);

      }
    }
  }

  public updateParcelStatus(event) {
    this.parcelStatus = "";
    //this.parcelStatus +=JSON.stringify(this.web3.contractDetails).toString().substr(0,20);
    this.parcelStatus += "";
    this.parcelStatus += event;
    this.parcelStatus += " , DETAILS: ";
    this.parcelStatus += this.requiredFormGroup.value.status1.toString();
    this.parcelStatus += ", TIMESTAMP : ";
    this.parcelStatus += new Date().toDateString();
    this.parcelStatus+="#";

    console.log(this.parcelStatus);

    if (this.parcelStatus.length > 31) {
      for (let i = 0; i < this.parcelStatus.length; i += 31) {
        if (i >= this.parcelStatus.length) {
          this.web3.updateParcelStatus(this.parcelID, this.parcelStatus.substr(i, this.parcelStatus.length));
          let message = this.parcelStatus.substr(i, this.parcelStatus.length);
          console.log("message last: "+ message);
          console.log(message.length);
        }
        else {
          let message = this.parcelStatus.substr(i, 31);
          console.log("message: "+ message);
          console.log(message.length);
          
          
          this.web3.updateParcelStatus(this.parcelID, message);
        }
      }
    }
    else {
      if (this.parcelID != null && this.parcelStatus != null) {
        this.web3.updateParcelStatus(this.parcelID, this.parcelStatus.substr(0, 31));
        this.web3.updateParcelStatus(this.parcelID, "");
        this.parcelStatus = "";
      }
    }
    this.web3.updateParcelStatus(this.parcelID, "#");
    this.showParcelStatus();
  }

}
