import { Injectable } from '@angular/core';
import { CanActivate, Router, ActivatedRouteSnapshot, RouterStateSnapshot, ActivatedRoute } from '@angular/router';
import { Observable, Subject } from 'rxjs';
import { LoginService } from './login.service';
import { LoginViewComponent } from './login-view/login-view.component';



@Injectable()
export class LoginGuardService implements CanActivate {
  result = false;
  params: any;
  url: any;
  constructor(private router: Router, private activatedRoute: ActivatedRoute) {
    this.activatedRoute.queryParams
      .subscribe(params => {
        this.url = (params['/(data-app:client-view)'] || '/');
        console.log(this.url);
        
      });

  }

  canActivate(routeSnapshot: ActivatedRouteSnapshot, routerSnapshot: RouterStateSnapshot): Observable<boolean> | boolean {
    if (!this.result) {
      //this.router.navigate([{ outlets: { 'data-app': ['login'] } }], { relativeTo: this.activatedRoute });
      this.url = encodeURI(routerSnapshot.url);
      this.router.navigate([{ outlets: { 'data-app': ['client-view'] } }]);
    }

    return true;


  }

}
